<template>
    <div id="div1">
        <Loading v-if='loadding'/>
        <div style="width: 100%;" v-else>
            <div id="header">
                <div style="margin: auto 30px; height: 50px; line-height: 50px;">
                    <span class="title" 
                          style="float: left; height: 50px; line-height: 50px; margin-right: 1%;"
                        >学习小组</span>
                    <!-- <a-input-search id="header_input" 
                                    placeholder="请输入..." 
                                    @search="onSearch"></a-input-search> -->
                    <div class="input">
                        <a-tree-select
                            showSearch
                            style="width: 80%"
                            v-model="tableValue"
                            :treeData="tree_data"
                            :dropdownStyle="{ maxHeight: '400px', overflow: 'auto' }"
                            placeholder='请选择'
                            allowClear
                            treeDefaultExpandAll
                            @search = "Search_table"
                            @change = "Change_table"
                        >
                            <span style="color: #08c" slot="title" slot-scope="{key, value}">
                                {{value}}
                            </span>
                        </a-tree-select>
                    </div>
                    <div style="
                            float: left;
                            width: 1%;
                            height: 50px;
                            line-height: 50px;
                        ">
                        <a-button style="margin-left: 5%;" 
                                shape="circle" 
                                icon="search" 
                                @click="SendTable"
                            />
                    </div>
                    <div id="time_picker">
                        <span class="title" 
                          style="margin-right: 30px;"
                        >时间段</span>
                        <a-date-picker
                            :disabledDate="disabledStartDate"
                            showTime
                            format="YYYY-MM-DD"
                            v-model="startValue"
                            placeholder="开始时间"
                            @openChange="handleStartOpenChange"
                        />
                        <a-date-picker
                            :disabledDate="disabledEndDate"
                            showTime
                            format="YYYY-MM-DD"
                            placeholder="结束时间"
                            v-model="endValue"
                            :open="endOpen"
                            @openChange="handleEndOpenChange"
                        />
                    </div>
                </div>
            </div>
            <div id="C1">
                <a-table 
                    class="table_style"
                    :columns="column_data" 
                    :dataSource="table_data" 
                    :pagination="pagination"
                    :loading="loading"
                    rowKey = "team"
                    @change="handleTableChange"
                    bordered
                    >
                    <template slot="name" slot-scope="text">
                        <a href="javascript:;">{{text}}</a>
                    </template>
                <!-- <template slot="title" slot-scope="currentPageData">
                    Header
                </template>
                <template slot="footer" slot-scope="currentPageData">
                    Footer
                </template> -->
                </a-table>
                <div class="clearfloat"></div>
            </div>
        </div>
        <div id="bottom">
            <div style="margin: auto 30px; height: 50px;">
                <span class="title" 
                      style="float: left; height: 50px; line-height:50px; margin-right: 1%;"
                    >学习小组</span>
                    <!-- <a-input-search id="header_input" 
                                    placeholder="请输入..." 
                                    @search="onSearch"></a-input-search> -->
                <div class="input">
                    <a-tree-select
                        showSearch
                        style="width: 80%;"
                        v-model="chartValue"
                        :treeData="tree_data"
                        :dropdownStyle="{ maxHeight: '400px', overflow: 'auto' }"
                        placeholder='请选择'
                        allowClear
                        treeDefaultExpandAll
                        @search = "Search_chart"
                        @change = "Change_chart"
                        >
                        <span style="color: #08c" slot="title" slot-scope="{key, value}">
                            {{value}}
                        </span>
                    </a-tree-select>
                </div>
                <div style="margin: auto 30px; height: 50px;">
                <span class="title" 
                      style="float: left; height: 50px; line-height:50px; margin-right: 1%; margin-left: 1%;"
                    >对比项</span>
                    <div class="input">
                        <a-select 
                            showSearch
                            labelInValue
                            placeholder="请选择"
                            optionFilterProp="children"
                            style="width: 80%"
                            @change="SelectChange"
                            :filterOption="filterOption"
                            >
                            <a-select-option value="tip1">注册人数</a-select-option>
                            <a-select-option value="tip2">登陆人数</a-select-option>
                            <a-select-option value="tip3">人均选课数</a-select-option>
                            <a-select-option value="tip4">人均获得结业证书</a-select-option>
                            <a-select-option value="tip5">所选课程平均成绩</a-select-option>
                            <a-select-option value="tip6">所选课程平均学习进度</a-select-option>
                            <a-select-option value="tip7">人均获得学习积分</a-select-option>
                            <a-select-option value="tip8">人均总视频学习时长</a-select-option>
                            <a-select-option value="tip9">人均在线时长</a-select-option>
                            <a-select-option value="tip10">人均学习计划课程数</a-select-option>
                            <a-select-option value="tip11">人均学习计划课程完成百分比</a-select-option>
                        </a-select>
                    </div>
                    <div style="
                            float: left;
                            width: 1%;
                            height: 50px;
                            line-height: 50px;
                        ">
                        <a-button style="margin-left: 5%;" 
                                shape="circle" 
                                icon="search" 
                                @click="SendChart"
                            />
                    </div>
                </div>
            </div>
        </div>
        <div id="chart"
             ref="chart"
            >
        </div>
        <div style="height: 10px;"></div>
    </div>
</template>
<script>
import { request } from 'http';
export default {
    name: "echarts",
    data() {
        return{
            orgOptions: {},
            pagination:{
                "pageSize": 13,
                "showQuickJumper": true,
                // "showSizeChanger": true
            },
            treeExpandedKeys: [],
            radio_value: 0,
            loading: false,
            startValue: null,//时间段开始时间
            endValue: null,//时间段结束时间
            endOpen: false,
            title:[],
            table_data: [],//表格内数据
            tree_data: [],//选择栏的选项数组
            chartValue: '',//柱状图选择的学习小组
            tableValue: '',//表格选择的学习小组
            selectValue: '',//对比项的选择
            loadding: true,
            column_data: [
                {
                    title: '学习小组',
                    dataIndex: 'team',
                    // sorter: true,
                    // scopedSlots: { customRender: 'team' },
                    width: '12%',
                    align: 'center'
                }, {
                    title: '注册人数',
                    dataIndex: 'tip1',
                    width: '8%',
                    align: 'center'
                }, {
                    title: '登陆人数',
                    dataIndex: 'tip2',
                    width: '8%',
                    align: 'center'
                },{
                    title: '人均选课数',
                    dataIndex: 'tip3',
                    width: '8%',
                    align: 'center'
                },{
                    title: '人均获得结业证书',
                    dataIndex: 'tip4',
                    width: '8%',
                    align: 'center'
                },{
                    title: '所选课程平均成绩',
                    dataIndex: 'tip5',
                    width: '8%',
                    align: 'center'
                },{
                    title: '所选课程平均学习进度',
                    dataIndex: 'tip6',
                    width: '8%',
                    align: 'center'
                },{
                    title: '人均获得学习积分',
                    dataIndex: 'tip7',
                    width: '8%',
                    align: 'center'
                },{
                    title: '人均总视频学习时长',
                    dataIndex: 'tip8',
                    width: '8%',
                    align: 'center'
                },{
                    title: '人均在线时长',
                    dataIndex: 'tip9',
                    width: '8%',
                    align: 'center'
                },{
                    title: '人均学习计划课程数',
                    dataIndex: 'tip10',
                    width: '8%',
                    align: 'center'
                },{
                    title: '人均学习计划课程完成百分比',
                    dataIndex: 'tip11',
                    width: '8%',
                    align: 'center'
                },
            ],
            chart_x:[{
                    type: 'category',
                    data: ['注册人数','登陆人数','人均选课数','人均获得结业证书','所选课程平均成绩','所选课程平均学习进度','人均获得学习积分','人均总视频学习时长','人均在线时长','人均学习计划课程完成百分比']
                },
            ],
            chart_y:[{
                    type:'value'
                }
            ],
            chart_series:[
                {
                    name: "",
                    type: "bar",
                    label: {normal: {show: true, position: 'top', color: 'rgba(255,255,255,0.6)'}},
                    itemStyle: {
                        normal: {
                            barBorderRadius: 4,
                            color: {
                                type: 'linear',
                                x: 0,
                                y: 0,
                                x2: 0,
                                y2: 1,
                                colorStops: [{
                                    offset: 0, color: "#c23531"
                                }, {
                                    offset: 1, color: "#2f4554"
                                }]
                            }}},
                        barWidth: 20,
                        data: []
                    }
                ],            
        }
    },
    // mounted() {
    //     this.list_data = [
    //             {title:'Title 1'},
    //             {title:'Title 2'},
    //             {title:'Title 3'},
    //             {title:'Title 4'},
    //         ]
    // },
    mounted:function () {
            var _this = this;
            this.fetch();
            this.orgOptions = {
                tooltip: {
                    show: true
                },
                legend: {
                    orient: 'horizontal',
                    x: 'center',
                    y:'bottom',
                    data:[]
                },
                grid: {left: 60},
                xAxis: _this.chart_x[0],
                yAxis: _this.chart_y[0],
                series: _this.chart_series,
            };
            // this.$http.get(url).then(function(result) {
            //     _this.table_data = result.data.data.table_data
            //     console.log(_this.table_data)
            //     //console.log(result.data.data.table_data)
            // });
    },
    methods:{
        SendTable: function(){
            var table_value = this.tableValue;
            var start_value = this.startValue;
            var end_value = this.endValue;
        },
        SendChart: function(){
            var chart_value = this.chartValue;
            var select_value = this.selectValue;
        },
        SelectChange: function(e){
            this.selectValue = e;
        },
        Search_table: function(e){
            console.log(e)
        },
        Change_table: function(e){
            console.log(e)
        },
        Change_chart: function(e){
            console.log(e);
        },
        handleChange: function(e){
            console.log(e)
        },
        Search_chart: function(e) {
            var Chart = this.$refs.chart;
            let myChart = this.$echarts.init(Chart);
            var _this = this;
            var team_num = e.target.value;
            if(team_num < 3){
                this.push(0, (team_num - 1) * 2);
                this.push(1, team_num * 2 - 1);
                this.orgOptions.series = [this.chart_series[0],this.chart_series[1]];
                this.orgOptions.legend.data = [this.chart_series[0].name,this.chart_series[1].name];
            }
            else if(team_num === 3){
                this.push(0, (team_num - 1) * 2);
                this.push(1, (team_num + 1) * 2 - 1);
                this.orgOptions.series = [this.chart_series[0],this.chart_series[1]];
                this.orgOptions.legend.data = [this.chart_series[0].name,this.chart_series[1].name];
            }
            else if(team_num === 4){
                this.push(0, (team_num - 1) * 2);
                this.push(1, (team_num - 1) * 2 - 1);
                this.orgOptions.series = [this.chart_series[0],this.chart_series[1]];
                this.orgOptions.legend.data = [this.chart_series[0].name,this.chart_series[1].name];
            }
            else if(team_num === 5){
                this.push(0, (team_num - 1) * 2);
                this.push(1, team_num * 2 - 1);
                this.push(2, team_num * 2);
                this.orgOptions.series = this.chart_series;
                this.orgOptions.legend.data = [this.chart_series[0].name,
                                               this.chart_series[1].name,
                                               this.chart_series[2].name];
            }else{
                this.push(0, team_num * 2 - 1);
                this.push(1, team_num * 2);
                this.orgOptions.series = [this.chart_series[0],this.chart_series[1]];
                this.orgOptions.legend.data = [this.chart_series[0].name,this.chart_series[1].name];
            }
            myChart.clear();
            myChart.setOption(this.orgOptions, true);
        },
        push: function(p, id){
            var _this = this;
            var chart_data = [];
            this.chart_series[p].name = _this.table_data[id].team;
            chart_data = [
                    _this.table_data[id].tip1,
                    _this.table_data[id].tip2,
                    _this.table_data[id].tip3,
                    _this.table_data[id].tip4,
                    _this.table_data[id].tip5,
                    _this.table_data[id].tip6,
                    _this.table_data[id].tip7,
                    _this.table_data[id].tip8,
                    _this.table_data[id].tip9,
                    _this.table_data[id].tip10,
                    _this.table_data[id].tip11,
                ];
            this.chart_series[p].data = chart_data;
            chart_data = [];
        },
        handleTableChange: function(){

        },
        handleTableChange: function(pagination){
            const page = { ...pagination }
            page.current = pagination.current
            this.pagination = page
            this.fetch({
                results: pagination.pageSize,
                page: pagination.current,
                // sortField: sorter.field,
                // sortOrder: sorter.order,
            });
        },
        fetch: function(param = {}){
            // var url = "https://www.easy-mock.com/mock/5cc16f3602e10042035e4db0/term/table";
            // var _this = this;
            // this.loading = true;
            // this.$http.get(url).then(_this = (result) => 
            //     {
            //         const pagination = { ...this.pagination };
            //         this.loading = false;
            //         console.log(result.data.data);
            //         this.table_data = result.data.data.table_data;
            //         this.tree_data = result.data.tree_data;
            //         this.pagination = pagination;
            //     });
            
            //setTimeout(() => {this.loadding = false;}, 1500)
            var url = "/v2/5/VotaryChai?TRN-Api-Key=8d68e400-d537-4c9a-8740-5d18bd81ee36";
            var _this = this;
            this.loading = true;
            this.$http.get(url
                ).then(_this = (result) => 
                {
                    console.log(result);
                });
            
            setTimeout(() => {this.loadding = false;}, 1500)
        },
        disabledStartDate: function(startValue) {
            const endValue = this.endValue;
            if (!startValue || !endValue) {
                return false;
            }
            return startValue.valueOf() > endValue.valueOf();
        },
        disabledEndDate: function(endValue) {
            const startValue = this.startValue;
            if (!endValue || !startValue) {
                return false;
            }
            return startValue.valueOf() >= endValue.valueOf();
        },
        handleStartOpenChange: function(open) {
            if (!open) {
                this.endOpen = true;
            }
        },
        handleEndOpenChange: function(open) {
            this.endOpen = open;
        },
         filterOption(input, option) {
            return option.componentOptions.children[0].text.toLowerCase().indexOf(input.toLowerCase()) >= 0
        }
    }
}
</script>
<style>
html, body, #div1 {
    width: 100%;
    margin: 0px;
    padding: 0px;
    text-align: center;
}
#C1{
    display: inline-block;
    position: relative;
    width: 100%;
    margin:0 auto;
}
#C1 td{
    height: 10px;
    line-height: 10px;
    font-size: 14px;
}
#C1 th{
    background-color: rgb(240, 245, 247);
}
#header{
    width:100%;
    height:50px;
}
#bottom{
    width:100%;
    height:50px;
}
#chart{
    width: 98%;
    height: 400px;
    margin-left: 1%; margin-right: 1%; 
    margin-top:10px;
    margin-right: 1%;
    border: 1px solid black;
}
#radio_group{
    position: absolute;
    display: inline-block; 
    text-align: left; 
    height: 180px; width: 40%;  
    top: 0px; right: 0px;
    margin-right: 1%;
    border:1px solid black
}
#radio_group_inside{
    position: relative;
    text-align: left; 
    height: 180px; width: 100%;  
}
#radio_group .ant-radio-group{
    width: 70%
}
#time_picker{
    float: right;
    height: 50px; 
    line-height:50px;
}
.title{
    font-size: 17px;
}
.spin{
    position:absolute;
    top: 50%;
    left: 50%;
    transform:translate(-50%,-50%);
}
.button_show{
    margin-top: 10px;
}
.radio_style{
    display: block;
    left: 5%;
    height: 30px;
    line-Height: 30px;
}
.clearfloat{
    clear:both;
    height:0;
    font-size: 1px;
    line-height: 0px;
}
.table_style{
    width: 98%; 
    margin: 0 auto; margin-left: 1%; margin-right: 1%; 
    float: left;
}
.input{
    float: left;
    width: 10%;
    height: 50px;
    line-height: 50px;
}
table tr:nth-child(odd){   
    background: #f5f5f5;
}
</style>



