<template>
    <div style="width:100%;">
        <div id="header">
            <div style="height: 100px; line-height: 100px;">
                <a-input style="width: 30%;" placeholder="请输入需要查询的id" v-model="sendDate"/>
                <a-button style="margin-left: 1%;" 
                                    shape="circle" 
                                    icon="search" 
                                    @click="Send"
                                />
            </div>
        </div>
        <div id="details">
            <div class="form">
                <span class="title">反馈数据</span>
                <textarea class="content" v-model="returnData"></textarea>
            </div>
            <div class="form">
                <span class="title">错误信息</span>
                <textarea class="content" v-model="Err"></textarea>
            </div>
        </div>
    </div>
</template>
<script>
import { request } from 'http';
export default {
    name: "search",
    data() {
        return{
            sendDate: '',
            returnData: '',
            Err:''
        }
    },
    methods:{
        Send: function(){
            var _this = this;
            var url = "/v2/5/" + this.sendDate;
            this.loading = true;
            this.$http.get(url,
                {
                    headers:{
                        'TRN-API-Key' : '8d68e400-d537-4c9a-8740-5d18bd81ee36'
                    }
                }
                ).then(_this = (result) => 
                {
                    this.returnData = result;
                }
                ).catch((err) => {  
                    this.Err = err;
                });
            
            setTimeout(() => {this.loadding = false;}, 1500)
        }
    }
}
</script>

<style>
html, body, #div1 {
    width: 100%;
    margin: 0px;
    padding: 0px;
    text-align: center;
}
#header{
    width:100%;
    height:100px;
}
#details{
    width:100%;
    height:500px;
}
.form{
    float:left;
    margin:auto 2.5%;
    width:45%;
    height:500px;
}
.title{
    width:100%;
    height:30px;
    line-height:30px;
    font-size:20px;
}
.content{
    width:100%;
    height:470px;
    border: 1px solid black;
    overflow:scroll; 
}
</style>



